.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
ul{
  list-style: none;
}

.Main_div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid white;
    border-radius: 5px;
    padding: 5px;
    margin: 10px;
    margin-left: -25px;
    color:white;
    width: 350px;
    /* transform: translateY(-120px); */
    transform: translateY(70px);
    height: 70px;
}
li{
  border: none;
  font-size: 22px;
  margin-left: 20px;
}
.list_div{
     overflow-y: auto;
     height: 500px;
     display: flex;
    /* box-shadow: 0 8px 15px rgb(76, 243, 21); */
      flex-direction: column;
     justify-content: center;
     align-items: center;
     border: 4px solid rgb(76, 243, 21);
     border-radius: 10px;
     width: 400px;
}
h1{
  color:white;
  font-family:  sans-serif;
  font-size: 32px;
}
.Con{
  /* border:2px solid white; */
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 50%;
  /* box-shadow: 0 4px 15px rgb(76, 243, 21); */
  height: 80%;
}
body{
  width: 100%;
   font-family:  sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to left,rgb(47, 137, 210),rgb(236, 188, 74));
}
.Container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 950px;
  backdrop-filter: blur(10px);
  background: transparent;
  box-shadow: 0 21px 32px -7px;
  height: 750px;
  border-radius: 10px;
 

}
.Form{
  width: 400px;
  border-radius: 10px;
  border: 1px solid white;
  margin-bottom: 25px;
  display: flex;
  background:linear-gradient(to left,rgb(111, 238, 42),rgb(76, 243, 21));

  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 5px;
  box-shadow: 0 4px 15px rgb(13, 12, 12);
}
input{
  width: 200px;
  height: 70%;
  text-transform: capitalize;
  text-align: center;
  background: transparent;
  border: 2px solid white;
  color:white;
  outline: none;
}
#formbutton{
  width: 70px;
  background: transparent;
  color:white;
  font-size: 22px;
  border: 2px solid white;
  border-radius: 20px;
  margin-right: 20px;
  height: 70%;
}
#twos_2{
  width: 40px;
  height: 40px;
  background: transparent;
  border-radius: 5px;
  color:white;
  font-size: 22px;
  font-family: fantasy;
  margin: 5px;
}
#twos_1{
  width: 80px;
  height: 40px;
   font-size: 22px;
   color:white;
   text-transform: capitalize;
  font-family: fantasy;
  background: transparent;
  border-radius: 5px;
  margin: 5px;
}